/* styles.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

header {
    background-color: #333;
    color: #fff;
    padding: 20px;
}

nav ul {
    list-style-type: none;
    padding: 0;
}

nav ul li {
    display: inline;
    margin-right: 10px;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
}

main {
    padding: 20px;
}

.tiles {
    display: flex;
    flex-wrap: wrap;
}

.tile {
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    margin: 10px;
    width: calc(33.33% - 20px); /* Three tiles in a row */
    box-sizing: border-box; /* Include padding and border in the width calculation */
}

.tile h3 {
    margin-top: 0;
}

.tile p {
    margin-bottom: 0;
}

/* Responsive styling */
@media (max-width: 768px) {
    .tile {
        width: calc(50% - 20px); /* Two tiles in a row on smaller screens */
    }
}

@media (max-width: 480px) {
    .tile {
        width: 100%; /* Single tile in a row on even smaller screens */
    }
}


/* styles.css */
/* Existing styles */
/* .tiles, .tile, .tile h3, .tile p */

.tile-content {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center items horizontally */
    text-align: center; /* Center text content */
}

.tile-content img {
    max-width: 100%; /* Make the image responsive */
    height: auto; /* Ensure the aspect ratio is maintained */
    margin-bottom: 10px; /* Add space between image and text */
}


